@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap);
body{
	background-color: rgba(0,0,0);
}

.Greeting{
    padding: 15px;
    width: 220px;
    font-size: 2rem;
    font-family: 'Lato', sans-serif;
    color: white;
    letter-spacing: 6px;
}
.Date{
	color: white;
    opacity: 0.9;
    font-weight: bolder;
    -webkit-transform: translateY(-13px);
            transform: translateY(-13px);
    margin-left: 15px;

}

.Hour{
    color:white;
    font-size: 2rem;
    font-family: 'Lato', sans-serif;
    margin-left: 15px;

}

.City{
    color: white;
    font-size: 4rem;
    -webkit-transform: translateY(2rem);
            transform: translateY(2rem);
    font-family: 'Lato', sans-serif;
}

.Grades{
    color: white;
    font-size: 7rem;
    -webkit-transform: translateY(2rem);
            transform: translateY(2rem);
    font-family: 'Lato', sans-serif;

}

.Other{
    display: grid;
    justify-items: center;
}

.Box{
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
    background-color:#f7faf8;
    padding: 30px;
    border-radius: 6px;
	box-shadow: black -12px 12px 13px 0px;
   
}

.Wind{
    font-size: 2rem;
}

.Pressure{
    font-size: 2rem;
}

.Humidity{
    font-size: 2rem;
}

.center{
    display: flex;
    justify-content: center;
    align-items: center;
}

.Center-Data{
    display: grid;

    text-align: center;
}

.weather{
	background-image: url(/app-weather/static/media/Night.5a575e16.png);
	object-fit: cover;
	background-size: cover;
	background-clip: content-box;
    opacity: 0.7;
    width: 380px;
    height: 660px;
    border-radius: 25px;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
    box-shadow: 2px 0 2px 5px #383535;
}

.weather.warm{
	background-image: url(/app-weather/static/media/Day.a7f83012.jpg); 
}



